#burst-8 {
    z-index: 1;
    flex: initial;
    /* width: 130px; */
    max-width: 12.666667%;
    height: 130px;
    position: relative;
    top:20px;
    text-align: center;
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    border: 3px gray solid;
    border-radius: 10px;
    background: url(/static/media/Contact_section_BG.166a955f.png);
  }

  #burst-8:before, #burst-8:after {
    border-radius: 10px;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 130px;
    width: 130px;
    -webkit-transform: rotate(125deg);
            transform: rotate(125deg);
    background: url(/static/media/Contact_section_BG.166a955f.png);
  }

  #burst-8:before {
    border-top: 3px gray solid;
    width: 130px;
    height: 130px;
    border-bottom: 3px gray solid;
  }

  #burst-8:after {
    font-family: 'Bungee Inline', cursive;  
    text-shadow: 2px 3px 0px rgba(0,0,0,0.15), 2px 2px 0px #f9ebd9;  
    vertical-align: middle;
    text-align: center;
    color: #f6edd8;
    font-size: 90px;
    letter-spacing: 0.94px;
    content: "PE";
    width: 130px;
    height: 130px;
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
  }
  .pe {
    text-align: center;
    color: #f6edd8;
    font-size: 188px;
    letter-spacing: 0.94px;
  }
 .navbar a:not([href]):not([tabindex]) {
    color: #f9ebd9;
  }

  .myPhotos {
    text-align: center;
  }

  #login {
      z-index: 1;
      font-size: 18px;
      padding-left: 0px;
      padding-right: 0px;
  }

  .nav-hr #hrUp {
    position: relative;
    top: -50px;
  }
